.first-meaningful {
  box-sizing: border-box;
  content: '';
  height: 287px;
  background-color: #fff;
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  padding-top: 98px;
}
.first-meaningful__logo {
  position: absolute;
  top: 15px;
  left: 15px;
}
.first-meaningful__logo .logo {
  width: 350px;
}
@media (min-width: 600px) {
}
@media (max-width: 599px) {
  .first-meaningful__logo .logo {
    display: none;
  }
}
.first-meaningful__logo img {
  height: 47px;
}
.first-meaningful__spinner {
  animation: 1s linear infinite svg-icon-spin;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 50px auto;
  width: 300px;
}
.first-meaningful__spinner svg {
  height: 72px;
  stroke-width: 1.66667;
  width: 72px;
  color: #333;
  display: inline-block;
  fill: currentColor;
  stroke: currentColor;
  stroke-linecap: round;
  stroke-linejoin: round;
  margin: 30px;
}
@media (min-width: 700px) {
}
@-webkit-keyframes svg-icon-spin {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes svg-icon-spin {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@media (min-width: 767px) {
}
@media (max-width: 767px) {
}
html,
body,
div,
img {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: inherit;
  vertical-align: baseline;
}
html {
  box-sizing: border-box;
  z-index: 1;
}
html,
body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
@font-face {
  font-family: Scotia Regular;
  src: url(data:font/woff2;base64,)
    format('woff2');
}
html,
body {
  font-family: Scotia Regular, Arial, Helvetica, 'sans-serif';
  font-weight: initial;
  letter-spacing: 0;
  line-height: 18px;
  margin: 0;
  color: #333;
}
.svg-icon-spinner {
  animation: svg-icon-spin 1s linear infinite;
}
@keyframes svg-icon-spin {
  0% {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@font-face {
  font-family: Scotia Regular;
  font-style: normal;
  src: url(data:font/woff2;base64,)
    format('woff2');
}
@media (min-width: 767px) {
}
html,
body {
  min-height: 100vh;
  font-size: 65.2%;
}
body {
  font-size: 1.6rem;
}

.sf-hidden {
  display: none !important;
}
