.password-validation .min-length,
.password-validation .upper-case,
.password-validation .lower-case,
.password-validation .numbers {
  color: #138468;
  font-weight: 500;
  font-family: Scotia Regular;
}

.password-validation span {
  font-size: 0.75em;
  color: #333;
}

.input.input--error {
  border-color: #be061b;
  padding-bottom: 1rem;
}

input[type='text'],
input[type='password'],
input[type='search'],
textarea {
  -webkit-appearance: none;
  appearance: none;
}

.input--text.input--error:focus {
  box-shadow: 0 0.1rem #be061b;
}

.svg-icon--size-18px {
  height: 1.8rem;
  stroke-width: 2;
  width: 1.8rem;
}

.svg-icon {
  display: inline-block;
  fill: currentColor;
  stroke: currentColor;
  stroke-linecap: round;
  stroke-linejoin: round;
}

.input-group__error {
  margin-left: .45rem;
}

.error {
  align-items: center;
  color: #be061b;
  display: flex;
  margin-top: .6rem;
}

.error__icon {
  align-self: flex-start;
}

.error__text {
  font-family: Scotia Bold;
  font-size: 1.4rem;
  font-weight: initial;
  line-height: 1.8rem;
  margin-left: 1rem;
